.ux-case-studies {
    background-color: var(--bg-color);
    color: var(--text-color);
    padding: 4rem 2rem;
    text-align: center;
  }
  
  .ux-case-studies .intro {
    max-width: 600px;
    margin: 0 auto 2rem;
    font-size: 1.1rem;
  }
  
  .projects-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
  }
  
  .project-wrapper {
    flex: 1 1 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 400px;
  }
  
  .project-card {
    background-color: white;
    color: #111;
    padding: 1rem;
    border-radius: 1rem;
    box-shadow: 0 10px 20px rgba(0,0,0,0.1);
    transition: transform 0.3s ease;
    height: 100%;
  }
  
  .project-card:hover {
    transform: translateY(-10px);
  }
  
  .project-card img {
    max-width: 100%;
    height: auto;
    border-radius: 0.75rem;
  }
  
  .web-project-buttons {
    display: flex;
    justify-content: center;
    margin-top: 1rem;
  }
  
  .btn-demo {
    padding: 1rem 2rem;
    background-color: var(--button-bg);
    color: var(--text-color);
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    font-weight: bold;
    margin: 0.5rem;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .btn-demo:hover {
    background-color: wheat;
    color: black;
    transform: translateY(-5px);
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
  }
  