.footer {
    background-color: wheat;
    color: var(--bg-color);
    padding: 1.5rem 1rem;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .footer p {
    margin: 0.5rem 0;
    font-size: 0.95rem;
  }
  
  .social-icons {
    display: flex;
    gap: 1rem;
    margin-top: 0.5rem;
  }
  
  .social-icons a {
    color: var(--bg-color);
    font-size: 1.5rem;
    transition: color 0.3s ease;
  }
  
  .social-icons a:hover {
    color: #00bfff; /* Light Blue on hover */
  }
  @media (max-width: 768px) {
    .footer-links {
      flex-direction: column;
      align-items: center;
    }
  
    .footer-links a {
      margin-bottom: 10px;
    }
  
    .footer .social-icons a {
      font-size: 25px; /* Increase icon size on smaller screens */
      margin: 0 15px;
    }
  }
  