.about-section {
    background-color: var(--bg-color);
    color: var(--text-color);
    padding: 2rem 1rem;
    display: flex;
    line-height: 1.7;
    max-width: 800px;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    text-align: center;
  letter-spacing: 0.3px;
  }
  
  .about-content {
    max-width: 800px;
  }
  .about-section h2 {
    font-size: 2rem;
    margin-bottom: 2rem;
    color: --text-color;
  }
  .about-section p{
    font-size: 1rem;
    margin-bottom: 1.5rem;
    color: #cccccc;
  }
  @media (max-width: 768px) {
    .about-section {
      padding: 40px 15px;
    }
  
    .about-section h2 {
      font-size: 24px; /* Smaller heading on mobile */
    }
  
    .about-section p {
      font-size: 17px;
      margin-bottom: 20px;
    }
  
    .about-section img {
      max-width: 150px;
      margin-top: 20px;
    }
  }
  