.skills-section {
    background-color: var(--bg-color);
    color: var(--text-color);
    padding: 4rem 2rem;
    text-align: center;
  }
  
  .skills-section h2 {
    font-size: 2rem;
    margin-bottom: 2rem;
    color: white;
  }
  
  .skills-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
  }
  
  .skill-item {
    background-color: #1a1a1d;
    padding: 1rem 1.5rem;
    border-radius: 12px;
    box-shadow: 0 0 10px rgba(98, 70, 234, 0.2);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--text-color);
    width: 120px;
  }
  
  .skill-item:hover {
    transform: scale(1.1);
    box-shadow: 0 0 20px rgba(98, 70, 234, 0.5);
  }
  
  .skill-item svg {
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
    color: var(--accent-color);
  }
  
  .skill-item span {
    font-weight: 600;
    margin-top: 5px;
  }
  