.home {
    background-color: var(--bg-color);
    color: var(--text-color);
    padding: 5rem 1rem;
    text-align: center;
    position: relative;
    overflow: hidden;
    z-index: 1;
  }

  .home::before,
.home::after {
  content: '';
  position: absolute;
  border-radius: 50%;
  filter: blur(150px);
  opacity: 0.4;
  z-index: 0;
}

.home::before {
  width: 400px;
  height: 400px;
  top: -100px;
  left: -100px;
}

.home::after {
  width: 300px;
  height: 300px;
  bottom: -80px;
  right: -80px;
}

.home h1,
.home p,
.home-btn {
  position: relative;
  z-index: 2;
}
  .home h1 {
    font-size: 3rem;
    margin: 1rem 0;
  }
  
  .home p {
    font-size: 1.5rem;
  }
  
  .home-btn {
    padding: 1rem 1rem;
    background-color: var(--button-bg);
    color: var(--text-color);
    border: 1px solid black;
    border-radius: 7px;
    font-size: 1rem;
    font-weight: bold;
    margin: 0.5rem;
    cursor: pointer;
    transition: all 0.3s ease;
    transform: translateY(0);
  }
  
  
  .home-btn:hover {
    background-color: wheat; 
    color: black;
    transform: translateY(-5px);
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
  }
  
  /* Mobile responsiveness */
@media (max-width: 768px) {
    .home-section {
      padding: 40px 15px;
    }
  
    .home-section h1 {
      font-size: 28px; /* Smaller heading on mobile */
    }
  
    .home-section p {
      font-size: 16px;
      margin-bottom: 20px;
    }
  
    .home-section .cta-button {
      font-size: 14px;
      padding: 10px 20px;
    }
  }