/* src/styles/contact.css */

.contact-section {
    background-color: var(--bg-color);
    color: var(--text-color);
    padding: 3rem 1rem;
    text-align: center;
  }
  
  .contact-form {
    max-width: 500px;
    margin: 0 auto;
  }
  
  .contact-form input,
  .contact-form textarea {
    width: 100%;
    padding: 1rem;
    margin: 0.5rem 0;
    border-radius: 5px;
    border: 1px solid #ccc;
    background-color: #fff;
    color: #333;
    font-size: 1rem;
    resize: vertical;
  }
  

  .contact-form button {
    width: 100%;
    padding: 1rem;
    background-color: var(--button-bg);
    color: var(--text-color);
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    font-weight: bold;
    margin-top: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
  }
  
  .contact-form button:hover {
    background-color: wheat;
    transform: scale(1.05);
    color: black;
  }
  

/* Mobile responsiveness */
@media (max-width: 768px) {
  .contact-form {
    width: 100%;
    padding: 0 1rem;
  }

  .contact-form input,
  .contact-form textarea {
    font-size: 1rem;
  }

  .contact-form button {
    font-size: 1rem;
  }
}

  *, *::before, *::after {
    box-sizing: border-box;
  }
  