.header {
    padding: 1rem;
    background: var(--bg-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .logo {
    width: 100px;
    height: auto;
  }
  
  nav a {
    color: var(--text-color);
    margin: 0 1rem;
    text-decoration: none;
    font-weight: 500;
  }
  nav a:hover {
    color: wheat;
    transition: 130ms;
    cursor: pointer;
  }

  .header nav .react-scroll-link {
    cursor: pointer;
  }
  
  
  .download-btn {
    background-color: var(--button-bg);
    padding: 8px 16px;
    border-radius: 8px;
    border: 1px solid var(--accent-color);
    text-decoration: none;
    font-weight: 500;
    transition: background-color 0.3s ease;
  }
  
  .download-btn:hover {
    background-color: wheat;
    color: black
  }
  
  /* src/components/Header.css */
nav ul {
    display: flex;
    justify-content: space-between;
    justify-content: space-evenly;
    list-style: none;
    padding: 0;
  }
  
  nav ul li {
    margin: 0 15px;
  }
  
  nav ul li a {
    text-decoration: none;
    color: #333;
    font-size: 16px;
  }
  
  @media (max-width: 768px) {
    nav ul {
      flex-direction: column;
      align-items: center;
    }
  
    nav ul li {
      margin: 10px 0;
    }
  
    nav ul li a {
      font-size: 18px;
    }
  }
  /* Hamburger button */
.hamburger {
  display: none;
  font-size: 2rem;
  background: none;
  border: none;
  color: var(--text-color);
  cursor: pointer;
  z-index: 1100;
}

/* Nav container default */
.nav {
  display: flex;
  gap: 1rem;
  align-items: center;
}

/* Mobile styles */
@media (max-width: 768px) {
  .hamburger {
    display: block;
  }

  .nav {
    display: none;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 60px; /* adjust based on header height */
    left: 0;
    width: 100%;
    background: var(--bg-color);
    padding: 1rem 0;
    z-index: 1000;
  }

  .nav.open {
    display: flex;
  }

  .nav a,
  .nav .download-btn {
    font-size: 18px;
    margin: 10px 0;
  }
}
