.work-section {
    background-color: var(--bg-color);
    color: var(--text-color);
    padding: 4rem 1rem;
    text-align: center;
  }
  
  .project-gallery {
    display: flex;
    justify-content: center;
    gap: 2rem;
    flex-wrap: wrap;
    margin-top: 2rem;
    margin-bottom: 2rem;
  }
  
  .project-card {
    max-width: 300px;
    margin: 1rem;
  }
  
  .project-card img {
    width: 100%;
    height: auto;
    border-radius: 10px;
  }
  
  .work-buttons {
    margin-top: 4rem; /* increase spacing */
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 1rem;
    position: relative; /* ensures it's not overlapping floating elements */
    z-index: 1;
  }
  
  
  .work-button {
    padding: 1rem 2rem;
    background-color: var(--button-bg);
    color: var(--text-color);
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    font-weight: bold;
    margin: 0.5rem;
    cursor: pointer;
    transition: all 0.3s ease;
    transform: translateY(0);
  }
  
  .work-button:hover {
    background-color: wheat; 
    color: black;
    transform: translateY(-5px);
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
  }
  .work-section h2 {
    font-size: 2rem;
    margin-bottom: 2rem;
    color: --text-color;
  }
  .project-card-link {
    text-decoration: none;
    color: inherit;
  }
  .project-card:hover {
    transform: translateY(-5px);
    transition: all 0.3s ease;
  }

  /* src/components/Work.css */
.work-section {
    padding: 40px;
  }
  
  @media (max-width: 768px) {
    .work-section {
      padding: 20px;
    }
  }
  